.Members {
  figure {
    background: rgb(255, 164, 255);
  }
  #login {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    form {
      width: 48%;
      position: relative;
      word-break: break-all;
      fieldset {
        width: 90%;
        position: relative;
        margin-bottom: 20px;
      }
      .field {
        position: relative;

        label {
          color: #555;
          font: 18px/30px 'noto sans';
          font-weight: 100;
          position: absolute;
          left: 0;
          top: 0;
          padding: 0 5px;
          transition: 0.2s;
        }
        > input[type='text'],
        input[type='password'] {
          font-size: 16px;
          display: block;
          width: 100%;
          height: 30px;
          padding: 0 5px;
          color: #555;
          border: 0;
          border-bottom: 1px solid #ccc;
          outline: none;
          margin-top: 50px;
          &:focus ~ label,
          &:not(:placeholder-shown) ~ label {
            top: -30px;
            font-size: 18px;
          }
          &:focus {
            border-bottom: 1px solid hotpink;
          }
        }
        .err {
          margin-top: 5px;
          font: bold 14px/1 'roboto';
          font-weight: 300;
          color: hotpink;
        }
      }
      .field2 {
        position: relative;
        margin-top: 50px;
        .radio,
        .checkbox {
          width: 100%;
          height: 30px;
          padding: 0 5px;
          border-bottom: 1px solid #ccc;
          display: flex;
          p {
            color: #555;
            font: 18px/30px 'noto sans';
            font-weight: 100;
            margin-right: 50px;
          }
          label {
            color: #555;
            font: 15px/30px 'roboto';
            font-weight: 300;
          }
          input[type='radio'],
          input[type='checkbox'] {
            margin: 0 20px 0 10px;
          }
        }
        > .err {
          margin-top: 5px;
          font: bold 14px/1 'roboto';
          font-weight: 300;
          color: hotpink;
        }
      }
      .field3 {
        position: relative;
        margin-top: 50px;
        .select {
          width: 100%;
          height: 30px;
          padding: 0 5px;
          border-bottom: 1px solid #ccc;
          display: flex;
          > label {
            color: #555;
            font: 18px/30px 'noto sans';
            font-weight: 100;
            margin-right: 50px;
          }
          select {
            border: none;
            width: 60%;
            color: #555;
            font: 15px/30px 'roboto';
            font-weight: 300;
            outline: none;
          }
        }
        > .err {
          margin-top: 5px;
          font: bold 14px/1 'roboto';
          font-weight: 300;
          color: hotpink;
        }
      }
      .btnset {
        width: 100%;
        padding: 15px;
        margin-top: 50px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        > input {
          border: 1px solid #aaa;
          width: 150px;
          height: 30px;
          margin: 0 10px;
        }
      }
    }
    .login_Img {
      width: 48%;
      height: 90vh;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

@media screen and (max-width: $tablet) {
  .Members {
    #login {
      form {
        width: 58%;
      }

      .login_Img {
        width: 38%;
      }
    }
  }
}

@media screen and (max-width: $mobile) {
  .Members {
    #login {
      form {
        width: 100%;
        .field2 {
          .checkbox {
            width: 100%;
            height: 70px;
            flex-wrap: wrap;
            p {
              width: 100%;
            }
            input[type='radio'],
            input[type='checkbox'] {
              margin: 0 20px 0 10px;
            }
          }
          > .err {
            margin-top: 5px;
            font: bold 14px/1 'roboto';
            font-weight: 300;
            color: hotpink;
          }
        }
      }

      .login_Img {
        width: 100%;
        height: 60vh;
      }
    }
  }
}
